import React from 'react';
import styles from './TypeSelector.module.css';

const objectTypes = [
  {
    id: 'camps',
    name: ' ',
    icon: '',
    description: '     ',
    color: '#48bb78'
  },
  {
    id: 'resorts',
    name: ' ',
    icon: '',
    description: '   ',
    color: '#4299e1'
  },
  {
    id: 'sanatoriums',
    name: '',
    icon: '',
    description: '  ',
    color: '#9f7aea'
  },
  {
    id: 'sports',
    name: '',
    icon: '', //  '', '', ''
    description: '    ',
    color: '#f56565' //  
  },
  {
    id: 'culture',
    name: '',
    icon: '', //  '', '', ''
    description: ',    ',
    color: '#9f7aea' //  
  }
];

const   TypeSelector = ({ onSelect }) => {
  return (
    <div className={styles.selector}>
      <div className={styles.hero}>
        <h1 className={styles.heroTitle}>     </h1>
        <p className={styles.heroSubtitle}>      </p>
      </div>

      <div className={styles.typesGrid}>
        {objectTypes.map((type) => (
          <div
            key={type.id}
            className={styles.typeCard}
            onClick={() => onSelect(type.id)}
            style={{ '--card-color': type.color }}
          >
            <div className={styles.cardContent}>
              <div className={styles.iconWrapper}>
                <span className={styles.icon}>{type.icon}</span>
              </div>
              <h3 className={styles.cardTitle}>{type.name}</h3>
              <p className={styles.cardDescription}>{type.description}</p>
              <div className={styles.cardHover}>
                <span className={styles.hoverText}></span>
                <span className={styles.arrow}></span>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default TypeSelector;