import React, {useState} from 'react';
import {useParams, Link} from 'react-router-dom';
import Fancybox from "../../shared/fancybox";
import styles from './ObjectDetails.module.css';
import resort1 from '../../assets/images/objects/resort1.jpg';
import resort2 from '../../assets/images/objects/resort2.png';
import room_1 from '../../assets/images/rooms/img.png';
import room_2 from '../../assets/images/rooms/img_1.png';
import room_3 from '../../assets/images/rooms/img_2.png';
import room_4 from '../../assets/images/rooms/img_3.png';
import room_5 from '../../assets/images/rooms/img_4.png';
import {MdOutlineKeyboardArrowDown, MdOutlineKeyboardArrowRight} from "react-icons/md";

// Mock  -       API
const mockObjects = {
  camps: [{
    id: 'camp1',
    name: ' ""',
    type: 'camps',
    location: ' , 15   ',
    rating: 4.5,
    price: 15000,
    image: '/camp1.jpg',
    description: '        .',
    photos: ['/camp1.jpg', '/camp2.jpg', '/camp3.jpg'],
    services: [{name: '', price: 15000}, {name: '', price: 5000}, {name: '', price: 3000},],
    contacts: {
      phone: '+7 (123) 456-78-90', email: 'sunny@camp.ru', address: ' , . 15'
    }
  }], resorts: [{
    id: 'resort1',
    name: '  " "',
    type: 'resorts',
    location: '  (),  ,  ' +
      ' ,    ',
    rating: 4.8,
    price: 2500,
    image: resort1,
    description: '-       . ' +
      '   30    .         ' +
      ' .   - (, , -, ,' +
      ' ,  ), ,    .      ' +
      '( 3 ),  (  5 ),  (  6 ).',
    photos: [resort1, resort2],
    services: [{name: '  4 ', price: 5000}, {name: '', price: 2000}, {
      name: ' ',
      price: 1000
    },],
    contacts: {
      phone: '+7 (987) 654-32-10',
      email: 'newLand@,mail.ru',
      address: '  (),  ,  ' +
        ' ,    "'
    },
    mapEmbedUrl: 'https://yandex.ru/map-widget/v1/?um=constructor%3A1a2b3c...',
    howToGet: ' : ,  ,  .   -239 -.  ' +
      ':  2237      "   ".',
    rooms: [{
      id: 'room1',
      name: '',
      description: ' -   , -,   . ' +
        '    ,   .  ' +
        '   ,    .',
      price: 3500,
      capacity: 3,
      amenities: ['Wi-Fi', 'TV', '', ''],
      images: [room_1, room_2, room_3, room_4, room_5],
    }, {
      id: 'room1',
      name: 'SUITE (C,  )',
      description: '     ',
      price: 4500,
      capacity: 4,
      amenities: ['Wi-Fi', 'TV', '', ''],
      images: [room_1, room_2, room_3, room_4, room_5],
    }, {
      id: 'room1',
      name: '',
      description: '     ',
      price: 6000,
      capacity: 4,
      amenities: ['Wi-Fi', 'TV', '', ''],
      images: [room_1, room_2, room_3, room_4, room_5],
    }
      // ...   ...
    ]
  }], sanatoriums: [{
    id: 'san1',
    name: ' ""',
    type: 'sanatoriums',
    location: ' , . , 1',
    rating: 4.7,
    price: 3500,
    image: '/san1.jpg',
    description: '       .',
    photos: ['/san1.jpg', '/san2.jpg', '/san3.jpg'],
    services: [{name: ' ', price: 3500}, {
      name: ' ',
      price: 5000
    }, {name: 'SPA-', price: 3000},],
    contacts: {
      phone: '+7 (555) 123-45-67', email: 'info@zdorovie.ru', address: ' , . , 1'
    }
  }]
};

const ObjectDetails = () => {
  const {id} = useParams();
  const [currentPhotoIndex, setCurrentPhotoIndex] = useState(0);

  const [expandedSections, setExpandedSections] = useState({
    about: true,
    services: true,
    rooms: true,
    map: true,
    contacts: true
  });

  const toggleSection = (section) => {
    setExpandedSections(prev => ({
      ...prev,
      [section]: !prev[section]
    }));
  };


  //    ID (   -   API)
  let object = null;
  for (const type in mockObjects) {
    const found = mockObjects[type].find(obj => obj.id === id);
    if (found) {
      object = found;
      break;
    }
  }

  if (!object) {
    return (<div className={styles.notFound}>
      <h2>  </h2>
      <Link to="/" className={styles.backLink}>  </Link>
    </div>);
  }

  const nextPhoto = () => {
    setCurrentPhotoIndex((prev) => prev === object.photos.length - 1 ? 0 : prev + 1);
  };

  const prevPhoto = () => {
    setCurrentPhotoIndex((prev) => prev === 0 ? object.photos.length - 1 : prev - 1);
  };

  return (
    <div className={styles.details}>
      <Link to="/tatneft_care" className={styles.backLink}>   </Link>

      <h1 className={styles.title}>{object.name}</h1>
      <p className={styles.location}>{object.location}</p>

      <div className={styles.gallery}>
        <button className={styles.navButton} onClick={prevPhoto}></button>
        <Fancybox
          options={{
            Carousel: {
              infinite: true,
            },
          }}>
          <img data-fancybox="gallery"
               src={object.photos[currentPhotoIndex]}
               alt={object.name}
               className={styles.mainImage}
          />
          {object.photos.map((photo, index) => index !== currentPhotoIndex && (
            <img data-fancybox="gallery"
                 key={index}
                 src={photo}
                 alt={`${object.name} ${index + 1}`}
                 hidden
            />
          ))}
        </Fancybox>
        <button className={styles.navButton} onClick={nextPhoto}></button>
        <div className={styles.thumbnails}>
          {object.photos.map((photo, index) => (
            <img key={index}
                 src={photo}
                 alt={`${object.name} ${index + 1}`}
                 className={`${styles.thumbnail} ${index === currentPhotoIndex ? styles.active : ''}`}
                 onClick={() => setCurrentPhotoIndex(index)}
            />
          ))}
        </div>
      </div>

      {/*  " " */}
      <section className={styles.section}>
        <div className={styles.sectionHeader} onClick={() => toggleSection('about')}>
          <h2 className={styles.sectionTitle}> </h2>

          {expandedSections.about ? <MdOutlineKeyboardArrowDown size={25} className={styles.toggleIcon}/> :
            <MdOutlineKeyboardArrowRight size={25}/>}

        </div>
        {expandedSections.about && (
          <>
            <p>{object.description}</p>
            <div className={styles.rating}>
              : {''.repeat(Math.round(object.rating))} ({object.rating.toFixed(1)})
            </div>
          </>
        )}
      </section>

      {/*  "  " */}
      <section className={styles.section}>
        <div className={styles.sectionHeader} onClick={() => toggleSection('services')}>
          <h2 className={styles.sectionTitle}>  </h2>
          {expandedSections.services ? <MdOutlineKeyboardArrowDown size={25} className={styles.toggleIcon}/> :
            <MdOutlineKeyboardArrowRight size={25}/>}
        </div>
        {expandedSections.services && (
          <table className={styles.priceTable}>
            <thead>
            <tr>
              <th></th>
              <th></th>
            </tr>
            </thead>
            <tbody>
            {object.services.map((service, index) => (<tr key={index}>
              <td>{service.name}</td>
              <td>{service.price} </td>
            </tr>))}
            </tbody>
          </table>
        )}
      </section>

      {/*  "" */}
      {object.rooms && (
        <section className={styles.section}>
          <div className={styles.sectionHeader} onClick={() => toggleSection('rooms')}>
            <h2 className={styles.sectionTitle}>  </h2>
            {expandedSections.rooms ? <MdOutlineKeyboardArrowDown size={25} className={styles.toggleIcon}/> :
              <MdOutlineKeyboardArrowRight size={25}/>}
          </div>

          {expandedSections.rooms && (
            <div className={styles.roomsList}>
              {object.rooms.map((room, index) => (
                <React.Fragment key={room.id}>
                  <div className={styles.roomCard}>
                    <Fancybox
                      className={styles.roomGallery}
                      options={{
                        Carousel: {
                          infinite: true,
                        },
                      }}>
                      {room.images.map((img, idx) => (
                        <img
                          data-fancybox="gallery"
                          key={idx}
                          src={img}
                          alt={`${room.name}  ${idx + 1}`}
                          className={styles.roomImage}
                        />
                      ))}
                    </Fancybox>
                    <div className={styles.roomInfo}>
                      <h4>{room.name}</h4>
                      <p className={styles.roomDescription}>{room.description}</p>
                      <p className={styles.roomPrice}>{room.price} /</p>
                      <p>:  {room.capacity} </p>
                      <div className={styles.amenities}>
                        <h5>:</h5>
                        <ul>
                          {room.amenities.map((item, i) => (
                            <li key={i}>{item}</li>
                          ))}
                        </ul>
                      </div>
                    </div>
                  </div>
                  {/*    ,   */}
                  {index < object.rooms.length - 1 && (
                    <div className={styles.roomDivider}></div>
                  )}
                </React.Fragment>
              ))}
            </div>
          )}

        </section>
      )}

      {/*  "" */}
      <section className={styles.section}>
        <div className={styles.sectionHeader} onClick={() => toggleSection('map')}>
          <h2 className={styles.sectionTitle}> </h2>
          {expandedSections.map ? <MdOutlineKeyboardArrowDown size={25} className={styles.toggleIcon}/> :
            <MdOutlineKeyboardArrowRight size={25}/>}
        </div>
        {expandedSections.map && (
          <>
            <p>{object.howToGet}</p>
            <div className={styles.mapContainer}>
              <iframe
                src={object.mapEmbedUrl}
                width="100%"
                height="400"
                style={{border: 0}}
                allowFullScreen=""
                loading="lazy"
                title={` ${object.name}`}
              ></iframe>
            </div>
          </>
        )}
      </section>

      {/*  "" */}
      <section className={styles.section}>
        <div className={styles.sectionHeader} onClick={() => toggleSection('contacts')}>
          <h2 className={styles.sectionTitle}></h2>
          {expandedSections.contacts ? <MdOutlineKeyboardArrowDown size={25} className={styles.toggleIcon}/> :
            <MdOutlineKeyboardArrowRight size={25}/>}
        </div>
        {expandedSections.contacts && (
          <div className={styles.contactInfo}>
            <p><strong>:</strong> {object.contacts.phone}</p>
            <p><strong>Email:</strong> {object.contacts.email}</p>
            <p><strong>:</strong> {object.contacts.address}</p>
          </div>
        )}
      </section>
    </div>
  )
};

export default ObjectDetails;